//  IMPORTING LIBRARIES
import React from "react";
import axios from "axios";
import Movie from "./Movie";
import MovieForm from "./MovieForm";

//  DEFINING APP
class App extends React.Component {
  state = { movies: [] };

  //  THIS GETS THE DATA FROM API
  componentDidMount = async () => {
    const Response = await axios.get("http://localhost:5000//movies");

    this.setState({
      movies: Response.data.movies.map((movie) => (
        <Movie key={movie.title} movie={movie} />
      )),
    });
  };

  //  THIS TAKES THE DATA FROM MOVIEFOR AND POSTS IT TO THE API
  onFormSubmit = async (title, rating) => {
    const response = await fetch("http://localhost:5000/add_movies", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ title, rating }),
    });

    if (response.ok) {
      console.log("response accepted");
      this.componentDidMount(); // TO RERENDER COMPONENTS
    }
  };

  render() {
    return (
      <div style={{ display: "flex" }}>
        <div className="ui container" style={{ paddingLeft: "3%" }}>
          <h1 style={{ textAlign: "center", fontSize: "400%" }}>Form</h1>
          <MovieForm onFormSubmit={this.onFormSubmit} />
        </div>
        <div className="ui container">
          <h1 style={{ textAlign: "center", fontSize: "400%" }}>Movies</h1>
          {this.state.movies}
        </div>
      </div>
    );
  }
}

//  EXPORTING APP
export default App;
